@import url('https://fonts.googleapis.com/css2?family=Ovo&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@100&display=swap');

html {
    scroll-behavior: smooth;
}

/*
body {
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile.png");
}
*/

.body {
    font-family: "Ovo", Verdana;
    color: #000;
    background-color: #fff;
}

.home {
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: auto;
}

.header {
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 12px;
    position: relative;
    align-items: center;
    background-color: #fff;
}

.sitename {
    display: flex;
    font-family: 'Kanit';
    font-size: 30px;
    color: #23a9f2;
    font-weight: 800;
}

.menu-icon {
    height: 35px;
    width: 35px;
    font-size: 24px;
}

.menu-desktop {
    font-family: 'Kanit';
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: flex-end;
}

.menu-desktop-option {
    font-size: 16px;
    margin-right: 16px;
    font-weight: 700;
}

.menu-desktop-option a {
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.menu-option {
    height: 20px;
    width: 20px;
    margin-right: 4px;
}



.menu-mobile {
    font-family: 'Kanit';
    display: none;
}

.close-icon {
    height: 35px;
    width: 35px;
    display: flex;
    position: relative;
    z-index: 20;
}

.menu-mobile-options {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #E9F6FE;
    justify-content: center;
    align-items: center;
    height: 100%;
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    top: 0px;
    flex-direction: column;
    z-index: 1;
    border-radius: 15px;
}

.menu-mobile-option {
    font-size: 16px;
    margin-right: 16px;
    font-weight: 700;
    margin: 12px 0px;
    display: flex;
}

.menu-mobile-option a {
    text-decoration: none;
    color: black;
    display: flex;
}


.body {
    padding: 12px;
    padding-bottom: 36px;
}



.about-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.about-info {
    font-size: 24px;
}

.about-image {
    height: 420px;
}

.about-quote {
    font-size: 14px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-top: 7px;
}

.about a {
    text-decoration: none;
    color: #000;
}


.links {
    display: flex;
    justify-content: center;
    align-items: center;
}

.link {
    padding: 6px;
    cursor: pointer;
    margin: 6px;
}

.link-icon {
    height: 40px;
}



.portfolio {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.project {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}

.project-info {
    margin-right: 14px;
}

.project-title {
    font-size: 20px;
    font-weight: 600;
}

.project-links {
    display: flex;
    margin-top: 6px;
}

.project-links a {
    text-decoration: none;
    color: black;
}

.project-link-button {
    font-family: 'Kanit';
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 16px;
    margin-right: 6px;
}

.project-link-icon {
    height: 30px;
    display: flex;
    align-items: center;
    margin-right: 4px;
}

.project-tags {
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}

.project-tag {
    font-family: 'Kanit';
    font-weight: 600;
    font-size: 14px;
    padding: 4px 8px;
    background-color: #E9F6FE;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
}

.project-photo {
    width: 45%;
    border: 1px solid rgba(60, 64, 67, 0.25);
    border-radius: 15px;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.3);
}

.section-title {
    font-family: 'Kanit';
    font-size: 32px;
    font-weight: 700;
    margin: auto;
    margin-bottom: 25px;
}

.separator {
    background-image: linear-gradient(to right top,
    #1e8bc7, #1f90cf, #2096d7, #219bdf, 
    #22a1e7, #2da7eb, #37acef, #40b2f3, 
    #51b8f4, #60bff5, #6ec5f6, #7bcbf7);
    height: 5px;
    width: 85%;
    border-radius: 3px;
    margin: 70px 0px 25px;
    align-items: center;
}



.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.skills a {
    text-decoration: none;
    color: #23a9f2;
}

.skills-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
    margin-top: 15px;
}

.skills-section-title {
    font-size: 22px;
    font-weight: 600;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
}



.skill {
    display: flex;
    flex-direction: column;
    width: max-content;
    padding: 8px 16px;
    height: 140px;
    width: 140px;
    align-items: center;
    justify-content: center;
    margin: 6px;
}

.skill-icon {
    height: 50px;
}

.skill-name {
    font-size: 14px;
    font-family: 'Kanit';
    font-weight: 600;
}

.skill-score {
    font-size: 14px;
    font-family: 'Kanit';
    font-weight: 600;
}

.blog-preview {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.blog-preview a {
    text-decoration: none;
    color: #000;
    margin: auto;
}



.contact {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.contact p {
    margin: auto;
    margin-bottom: 20px;
}



.footer {
    font-family: 'Kanit';
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    padding: 12px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-top: 24px;
    background-color: #000;
}

.footer p {
    margin: 0px;
    margin-left: 15px;
}

.footer a {
    text-decoration: none;
    color: #fff;
    margin-left: 15px;
}



/* ------- BLOG PAGE -------*/

.blog {
    position: relative;
    margin: 0px; 
    padding: 0px;
    margin-top: 15px;
    padding-bottom: 45px;
    left: 0px;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile.png");
}

.blog a {
    text-decoration: none;
}

.blog-set {
    margin: 0px;
    margin-top: 15px;
    padding: 0px;
    display: grid;
    grid-gap: 10px;
    grid-auto-flow: row;
    grid-template-rows: repeat(2, auto);
    grid-template-columns: repeat(1, auto);
}

.blog-tile {
    color: black;
    text-decoration: none;
    margin-top: 15px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 15px;
    padding: 0px;
    font-size: 14px;
}

.blog-tile a {
    color: #000;
}

.blog-tile-header {
    font-family: 'Kanit', Helvetica;
    font-size: 14px;
    font-weight: 600;
    top: 0px;
    margin: 0px;
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 5px;
    background-color: #1f201e;
    border-radius: 15px 15px 0 0;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.3);
}

.blog-tile-header a {
    margin-left: 30px;
    color: white;
    display: inline-block;
  }

.blogstamp-overlay {
    text-align: left;
    display: block;
}

.blogstamp-overlay img {
    width: 30px;
    height: 30px;
    position: absolute;
    border-radius: 0px;
}

.blog-meta {
    font-family: 'Kanit', Helvetica;
    font-size: 15px;
    font-weight: 600;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: right;
    background-color: white;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.3);
}

.blog-meta a:visited {
    color: #000;
}

.blog-desc {
    font-family: 'Ovo', Helvetica;
    font-size: 12px;
    text-align: left;
    padding-left: 15px;
    padding-right: 15px;
    background-color: white;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.3);
}

.blog-desc p {
    margin: 0px;
}

.blog-tile-footer {
    top: 0px;
    height: 15px;
    margin: 0px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: white;
    border-radius: 0 0 15px 15px;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.3);
}  

.post {
    color: black;
    font-family: 'Titillium Web','Chakra Petch', Helvetica;
    position:relative;
    margin: 0px; 
    padding: 0px;
    margin-top: 15px;
    padding-bottom: 45px;
    left: 0px;
}
  
.post h1 {
    color: #23a9f2;
}
  
.post-body {
    text-align: left;
    margin: 0px;
    margin-top: 15px;
    padding: 0px;
    padding-bottom: 30px;
    background-color: white;
    box-shadow: 14px 14px rgba(60, 64, 67, 0.3);
}
  
.post-body p {
    font-family: 'Kufam', Verdana;
    font-size: 14px;
    padding-top: 3px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 3px;
}
  
.post-body h1 {
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
  
.post-body h2, h3 {
    margin-top: 20px;
    margin-bottom: 10px;
    padding-left: 20px;
}
  
.post-body h4 {
    font-size: 14px;
    font-family: 'Encode Sans Expanded', 'Kufam', Verdana;
    text-align: right;
    margin-bottom: 10px;
    padding-top: 10px;
    padding-right: 20px;
}
  
.post-body p {
    margin-top: 3px;
    margin-bottom: 3px;
}
  
.post-body img {
    max-width: 100%;
}
.post-body a {
    text-decoration: none;
    color: black;
}
  
.post-body ul {
    margin-top: 5px;
    margin-bottom: 16px;
    margin-left: 6px;
}
  
code {
    font-size: 14px;
    font-family: 'IBM Plex Mono', Courier;
    color: #d5d5d5;
    display: inline-block;
    white-space:pre-wrap;
    max-width: 100%; 
    word-break: break-all; 
    word-wrap: normal;
    margin-left: 20px;
    margin-right: 20px;
    padding: 16px;
    border-radius: 10px;
    background-color: #333333;
}
  


@media only screen and (min-width: 520px) {
    .blog-set {
        margin-left: 10px;
        margin-right: 10px;
        grid-gap: 2%;
        grid-template-rows: repeat(grid-auto-flow, 300px); /* Number of rows onscreen */
        grid-template-columns: repeat(2, 49%);
    }
    .blog-tile {
        margin-left: 10px;
        margin-right: 10px;
    }
}

@media only screen and (min-width: 900px) {
    .blog-set {
        grid-gap: 1%;
        grid-template-rows: repeat(grid-auto-flow, 250px); /* Number of rows onscreen */
        grid-template-columns: repeat(3, 32.66%);
    }
}

@media only screen and (min-width: 1110px) {  
    .blog-set {
        width: 1070px;
    }
}



/* ------------------------ */




@media only screen and (max-width: 1024px){
    .home {
        width: 90%;
    }
}

@media only screen and (max-width: 720px) {
    .menu-mobile {
        display: flex;
    }
    .menu-desktop {
        display: none;
    }
    .sitename {
        font-size: 24px;
    }
    .about-top {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-info {
        font-size: 20px;
    }
    .about-image {
        height: 280px;
    }
    .link-icon {
        height: 32px;
    }
    .project {
        flex-direction: column-reverse;
    }
    .project-info {
        margin-left: 0px;
        margin-top: 12px;
    }
    .project-photo {
        width: 90%;
    }
    .project-title {
        display: flex;
        justify-content: center;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .project-links {
        justify-content: center;
    }
    .project-tags {
        max-width: 100%;
        justify-content: center;
    }
    .skills-list {
        max-width: 100%;
        justify-content: center;
    }
    .skills-section {
        flex-direction: column;
        align-items: center;
    }
    .skill {
        width: 100px;
        height: 100px;
    }
    .skill-icon {
        height: 30px;
    }
}

@media only screen and (max-width: 512px){
    .home {
        width: 100%;
    }   
}